
@charset "UTF-8";
.cc_panel_wapper {
  margin-left: -15px;
  padding: 15px;
  box-sizing: border-box;
}
.cc_panel_wapper:after {
  content: "";
  display: block;
  clear: both;
}
.cc_panel_wapper .cc_panel_detail {
  position: relative;
  width: 50%;
  overflow: hidden;
  font-size: 0;
  float: left;
  padding-left: 15px;
  padding-bottom: 15px;
  box-sizing: border-box;
}
.cc_panel_wapper .cc_panel_detail_image_wapper {
  margin-bottom: 5px;
}
.cc_panel_wapper .cc_panel_detail_image_wapper .image {
  border-radius: 3px;
}
.cc_panel_wapper .cc_panel_detail_info .title {
  display: -webkit-box;
  height: 40px;
  margin-bottom: 18px;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  color: #2b333b;
  font-size: 12px;
  line-height: 1.6;
  font-weight: 400;
}
.cc_panel_wapper .cc_panel_detail_info .desc {
  position: absolute;
  left: 15px;
  bottom: 15px;
  color: #b7bbbf;
  font-size: 12px;
  line-height: 1;
}
.cc_panel_wapper .cc_panel_detail_info .desc i {
  vertical-align: -2px;
}



